import * as React from "react";
import styled from "styled-components";
import {
  Container,
  Typography
} from "@material-ui/core";

const ServerErrorMessage = () => {

  return (
    <ServerErrorMessageWrapper maxWidth="sm">
      <Text variant="h2">Sorry, our service is <br />currently unavailable.</Text>
      <br />
      <Text variant="h4">We'll be back soon! </Text>
      <br />
    </ServerErrorMessageWrapper>
  );
};

const ServerErrorMessageWrapper = styled(Container)`
&& {
    display: flex;
    flex-direction: column;
    text-align: center;
    max-width: 50%;
    padding-top: 10%;
  }
`;

const Text = styled(Typography)`
  && {
    color: rgba(0, 0, 0, 0.80);
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    &.MuiTypography-h2 {
      font-weight: bold;
    }
  }
`;

export default ServerErrorMessage;