import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button } from "@material-ui/core";
import logo from "../assets/logo.png";
import { Formik } from "formik";
import * as Yup from 'yup';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { useHistory } from 'react-router-dom';
//import { useMutation } from "@apollo/react-hooks";
//import { LOGIN_MUTATION } from '../services/UserService';
import { routeTo } from "../util/RoutesHelper";
import ServerErrorMessage from "../components/ServerErrorMessage";
import LoadingSpinner from '../components/LoadingSpinner';

const Home = (props) => {
    const history = useHistory();
    const handleLoginClick = () => {
        if (props && props.onLoginClick) {
          props.onLoginClick();
        } else {
          routeTo.login(history);
        }
    }

    const handleLogoutClick = () => {
        if (props && props.onLoginClick) {
          props.onLoginClick();
        } else {
          localStorage.clear();
        }
    }

    const handleRegisterClick = () => {
        if (props && props.onLoginClick) {
            props.onLoginClick();
        } else {
            routeTo.register(history);
        }
    }

    const handlePlaceClick = () => {
      if (props && props.onLoginClick) {
          props.onLoginClick();
      } else {
          routeTo.place(history);
      }
    }

    const handleValidationClick = () => {
      if (props && props.onLoginClick) {
          props.onLoginClick();
      } else {
          routeTo.emailValidation(history);
      }
  }

  const handleHomeClick = () => {
    if (props && props.onLoginClick) {
        props.onLoginClick();
    } else {
        routeTo.newHome(history);
    }
}
    
  return (
      <HomeContainer>
        <button onClick={handleLoginClick}>
            Login
        </button>
        <button onClick={handleLogoutClick}>
            Logout
        </button>
        <button onClick={handleRegisterClick}>
        Register
        </button>
        <button onClick={handlePlaceClick}>
        Place
        </button>
        <button onClick={handleValidationClick}>
        Validation
        </button>
        <button onClick={handleHomeClick}>
          Home
          </button>
    </HomeContainer>
  );
};

const HomeContainer = styled(Container)`
    && {
      display: flex;
      flex-direction: column;
      text-align: left;
      justify-content: center;
      height: 100vh;
    }
`;

export default Home;