import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button } from "@material-ui/core";
import logo from "../assets/logo.svg";
import { Formik } from "formik";
import * as Yup from 'yup';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { useHistory } from 'react-router-dom';
//import { useMutation } from "@apollo/react-hooks";
import { LOGIN } from '../services/UserService';
import { routeTo } from "../util/RoutesHelper";
import ServerErrorMessage from "../components/ServerErrorMessage";
import LoadingSpinner from '../components/LoadingSpinner';

const Login = (props) => {
  const [showPassword, changeShowPassword] = useState(false);
  const [errorMessages, setErrorMessages] = useState(null);
  const [loading, setLoading] = useState(false);
  const [login, { error }] = useState(null);
  let history = useHistory();
  if (error) return <ServerErrorMessage />
  

  const handleSubmit =  (values) => {   
    setLoading(true);
    const user = LOGIN(values.username, values.password);
    user.then(res => {
      console.log(res);
    if(res.error == undefined){
      localStorage.setItem('username',res.user.username);
      localStorage.setItem('token',res.user.token);
      routeTo.home(history)
    }else{
      console.log("error");
    }});
    setLoading(false);
  }

  const handleRegister = () => {
    if (props && props.onRegisterClick) {
      props.onRegisterClick();
    } else {
      routeTo.register(history);
    }
  }

  const handleHome = () => {
    routeTo.home(history);
  }

  return (
    <LoginContainer maxWidth="sm">
      <Logo src={logo} onClick={handleHome} />
      <br />
      <Formik
        initialValues={{
          username: "",
          password: ""
        }}
        onSubmit={async values => await handleSubmit(values)}
        validationSchema={Yup.object().shape({
          username: Yup.string()
            .required("Required"),
          password: Yup.string()
            .required("Required")
        })}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit,
          } = props;
          return (
            <form autoComplete="off" onSubmit={handleSubmit}>
              <FormContainer>
                {errorMessages ?
                  <FeedbackContainer>{errorMessages}</FeedbackContainer> : <></>
                }<br />
                <TextFieldCustom
                  id="username"
                  label="Username"
                  margin="normal"
                  variant="outlined"
                  placeholder="example@domain.com"
                  type="text"
                  value={values.username}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.username && touched.username
                      ? "text-input error"
                      : "text-input"
                  }
                  
                />
                {errors.username && touched.username && (
                  <FeedbackContainer>{errors.username}</FeedbackContainer>
                )}
                <br />
                <TextFieldCustom
                  id="password"
                  margin="normal"
                  variant="outlined"
                  label="Password"
                  type={showPassword ? 'text' : 'password'}
                  value={values.password}
                  placeholder="Enter your password"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.password && touched.password
                      ? "text-input error"
                      : "text-input"
                  }
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="Toggle password visibility"
                          onClick={() => changeShowPassword(!showPassword)}
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                />
                
                {errors.password && touched.password && (
                  <FeedbackContainer>{errors.password}</FeedbackContainer>
                )}
                <br/>
                {loading ?
                  <LoadingSpinner loading={loading} /> :
                  <ButtonCustom
                    variant="contained"
                    disabled={isSubmitting}
                    type="submit"
                    disableElevation
                  >
                    Login
                  </ButtonCustom>
                }
                <br />
                <NotRegisteredWrapper variant="h6">
                    Don't have an account?
                    <br/>
                    <button onClick={handleRegister}>
                      Sign Up
                    </button>
                  </NotRegisteredWrapper>
              </FormContainer>
            </form>
          );
        }}
      </Formik>
    </LoginContainer >
  );
};

const LoginContainer = styled(Container)`
  && {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    height: 100vh;
  }
`
const Logo = styled.img`
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  margin-bottom: 60px;
  cursor: pointer;
  width: 75%;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  margin-left: auto;
  margin-right: auto;
`;

const ButtonCustom = styled(Button)`
  margin-top: 10px;
  && {
    border-radius: 60px;
    min-width: 110px;
    background: #e24449;
    color: white;
    font-weight : bold;
    :hover {
      background: #e24449;
      color: white;
    }
  }
`;

const FeedbackContainer = styled.div`
  color: red;
`
const TextFieldCustom = styled(TextField)`
  width: 100%;
`

const NotRegisteredWrapper = styled(Typography)`
  && {
    color: rgba(0, 0, 0, 0.57);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    button {
      color: red;
      border: none;
      border-bottom: none;
      padding: 0px;
      margin-left: 5px;
      background-color: transparent;
      font-size: 1.8rem;
      outline: none;
      cursor: pointer;
      font-weight : bold;
    }
    button:hover{
      color:#e24449;
    }
  }
`;

export default Login;