import { items } from '../data/Dummy';
import React, { useContext } from "react";
import styled from "styled-components";
import {
    Typography, TextField, Button
} from "@material-ui/core";
import PlaceItem from '../components/PlaceItem';
import ProductInCart from '../components/ProductInCart';
import DrawerContent from '../components/DrawerContent';
import Masonry from "react-masonry-css";
import VerificationInput from 'react-verification-input'
import Drawer from "react-bottom-drawer";
import { CartContext } from '../contexts/CartContext';

const COLUMNS_BREAKPOINTS = {
  default: 2,
  1100: 2,
  700: 2,
};



const Place = () => {
  const [isVisible, setIsVisible] = React.useState(false);
  const { addProduct, cartItems, increase } = useContext(CartContext);
  const openDrawer = React.useCallback(() => setIsVisible(true), []);
  const closeDrawer = React.useCallback(() => setIsVisible(false), []);

  const totalPrice = () =>
  {
    var total = 0;
    for(var i = 0; i<cartItems.length;i++){
      total=total+(cartItems[i].quantity*cartItems[i].price);
    }
    return total;
  }

    return (
        <PlaceWrapper>
          <h2>Pizza</h2>
        <ProductsWrapper
                    breakpointCols={COLUMNS_BREAKPOINTS}
                    columnClassName="my-masonry-grid_column">
                    {items.map(item => {
                        return <PlaceItem
                            product={item}/>
                            
                    })}
          
          
        </ProductsWrapper>
        <button className="open-btn" onClick={openDrawer}>
          CART
        </button>

        <Drawer
                isVisible={isVisible}
                onClose={closeDrawer}
                hideScrollbars={true}
                >
                    <CartTitle >Cart</CartTitle>
                    {cartItems.map(item => {
                        return <ProductInCart
                            product={item}
                            />
                            
                    })}
                    <CartTitle >{"Total         "+"$"+totalPrice()}</CartTitle>
                    <ButtonCustom
                    variant="contained"
                    type="submit"
                    disableElevation
                  >
                    Next
                  </ButtonCustom>
            </Drawer>
        </PlaceWrapper>
      
    );
  };

  
 const CartTitle = styled(Typography)`
 &&{
  color:white;
  font-size: 36px
 }`

 const ButtonCustom = styled(Button)`
 margin-top: 10px;
 && {
  justify-content: center;
   width:80%;
   border-radius: 60px;
   min-width: 110px;
   background: #e24449;
   color: white;
   font-weight : bold;
   :hover {
     background: #e24449;
     color: white;
   }
 }
`;
  const VerificationInpu = styled(VerificationInput)`
  character {
    line-height: 50px;
    font-size: 36px;
    background-color: rgba(255, 255, 255, 0.2);
    border: 1px solid transparent;
    border-radius: 8px;
    color: white;
    margin-left: 8px;
  }

  .character--inactive {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: none;
  }
  
  .character--selected {
    border: 1px solid white;
  }
  `

  const PlaceWrapper = styled.div``;

  
const CategoryListItem = styled.div`
  margin: auto;
  margin-top: 10px;
  border: 1px solid rgba(0,0,0,.1);
  p{
    text-align: center;
    padding: 4px;
    font-family: monospace;
    background: #222;
    color: #efefef;
  }
  width: auto;
`;

const ProductsWrapper = styled(Masonry)`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: stretch;
  margin: auto;

    .my-masonry-grid_column {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-content: stretch;
      flex-grow: 1;
    }


`;

const Titlewrp = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  margin-right: auto;
`;

export default Place;