import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button,Grid } from "@material-ui/core";
import logo from "../assets/logo.svg";
import { Formik } from "formik";
import * as Yup from 'yup';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { useHistory } from 'react-router-dom';
//import { useMutation } from "@apollo/react-hooks";
import { LOGIN } from '../services/UserService';
import { routeTo } from "../util/RoutesHelper";
import ServerErrorMessage from "../components/ServerErrorMessage";
import LoadingSpinner from '../components/LoadingSpinner';
import CallIcon from '@material-ui/icons/Call';
import AppsIcon from '@material-ui/icons/Apps';

const Login = (props) => {
  const [showPassword, changeShowPassword] = useState(false);
  const [errorMessages, setErrorMessages] = useState(null);
  const [loading, setLoading] = useState(false);
  const [login, { error }] = useState(null);
  let history = useHistory();
  if (error) return <ServerErrorMessage />
  

 

  const handleHome = () => {
    routeTo.home(history);
  }

  const handleQRReaderClick = () => {
    if (props && props.onLoginClick) {
        props.onLoginClick();
    } else {
        routeTo.qrReader(history);
    }
  }

  return (
    <LoginContainer maxWidth="sm">
        <Logo src={logo} onClick={handleHome} />
        <br />
        <br />
        <Title>No tienes rdenes</Title>
        <br />
        <br />
        <SubTitle >Para encontrar comercios:</SubTitle>
        <Grid item xs={12}>
            <Grid container justify="center" spacing={4}>
                <Grid item>
                <CallIcon style={{fontSize: 130}} color="disabled"></CallIcon>
                <br/>
                <ButtonCustom>
                    Find By Phone
                </ButtonCustom> 
                </Grid>
                <Grid item>
                <AppsIcon style={{fontSize: 130}} color="disabled"></AppsIcon>
                <br/>
                <ButtonCustom onClick={handleQRReaderClick}>
                    Scan QR
                </ButtonCustom> 
                </Grid>
            </Grid>
        </Grid>
             
    </LoginContainer >
  );
};

const Title = styled(Typography)`
&&{
    text-align: center;
    font-size: 30px;
   }
`

const SubTitle = styled(Typography)`
&&{
 text-align: center;
 font-size: 20px;
 font-weight : bold;
}`

const LoginContainer = styled(Container)`
  && {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    height: 100vh;
  }
`

const Logo = styled.img`
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  margin-bottom: 60px;
  cursor: pointer;
  width: 75%;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  margin-left: auto;
  margin-right: auto;
`;

const ButtonCustom = styled(Button)`
  margin-top: 10px;
  && {
    width: 100%;
    border-radius: 60px;
    min-width: 110px;
    background: #e24449;
    color: white;
    font-weight : bold;
    :hover {
      background: #e24449;
      color: white;
    }
  }
`;

const FeedbackContainer = styled.div`
  color: red;
`
const TextFieldCustom = styled(TextField)`
  width: 100%;
`

const NotRegisteredWrapper = styled(Typography)`
  && {
    color: rgba(0, 0, 0, 0.57);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    button {
      color: red;
      border: none;
      border-bottom: none;
      padding: 0px;
      margin-left: 5px;
      background-color: transparent;
      font-size: 1.8rem;
      outline: none;
      cursor: pointer;
      font-weight : bold;
    }
    button:hover{
      color:#e24449;
    }
  }
`;

export default Login;