import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button, Grid } from "@material-ui/core";
const ProductInCart = (props) => {
    return (
        <Grid container wrap="nowrap" spacing={0}>
            <Grid item xs={1}>
                <ImageWrapper src={"https://placeralplato.com/files/2016/01/Pizza-con-pepperoni-640x480.jpg?width=1200&enable=upscale"}></ImageWrapper>
            </Grid>
            <Grid item xs={1}>
                <ProductName>{props.product.quantity}</ProductName>
            </Grid>
            <Grid item xs={6}>
                <ProductName>{props.product.name}</ProductName>
            </Grid>
            <Grid item xs={2}>
                <ProductName>{"$"+props.product.price*props.product.quantity}</ProductName>
            </Grid>
        </Grid>
    );
};

const ProductName = styled(Typography)`
 &&{
  color:white;
 }`

 const ImageWrapper = styled.img`
    width: 50px;
    height: 50px;
    border-radius: 50%;
 
`

export default ProductInCart;