import * as React from "react";
import styled from "styled-components";
import {
  Container,
  Typography
} from "@material-ui/core";
import logo from "../assets/404.png";

const NotFound = () => {

    return (
      <NotFoundWrapper maxWidth="sm">
        <Logo src={logo} />
        <NotRegisteredWrapper>
          <Text variant="h2">Oops!</Text>
          <br />
          <Text variant="h4">Page not found :(</Text>
          <br />
          <HomeButton>
            <a href="/"> HOME </a>
          </HomeButton>
        </NotRegisteredWrapper>
      </NotFoundWrapper>
    );
  };
  
  const NotFoundWrapper = styled(Container)`
    && {
      display: flex;
      flex-direction: column;
      text-align: left;
    }
  `;
  
  const Logo = styled.img`
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
    margin-bottom: 60px;
    margin-top: 20%;
    width: 200px;
    @media screen and (min-width: 500px){
      width: 350px;
      margin-bottom: 40px;
    }
  `;
  
  const NotRegisteredWrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
  `;
  
  const Text = styled(Typography)`
    && {
      color: rgba(0, 0, 0, 0.80);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
  
      &.MuiTypography-h2 {
        font-weight: bold;
      }
    }
  `;
  
  const HomeButton = styled.div`
    margin-top: 50px;
    background-color: #6202EE;
    height: 55px;
    width: 150px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  
    a {
       color: white ;
       font-size: 20px;
       text-decoration: none;
    }
    
    :hover {
      border-bottom: none;
      background-color: #3f51b5;
    }
  `;
  
  export default NotFound;
