export async function LOGIN(mail,pass) {
    const response = await fetch('https://akybay-back.herokuapp.com/login', 
    {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username: mail,
                               password: pass})
    });
    const json = await response.json();
    return json;
}

export async function REGISTER(user,pass,mail,country,zip){
	const response = await fetch('https://akybay-back.herokuapp.com/users',
	{
		method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({user: { username: user,
                               password: pass,
							   country_code: country,
							   zip_code: zip,
							   email: mail}})
    });
    const json = await response.json();
    return json;
}

export async function VALIDATEUSER(actCode,userid){
	const response = await fetch('https://akybay-back.herokuapp.com/users/'+userid+'/validate',
	{
		method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ code: actCode})
    });
    const json = await response.json();
    return json;
}

export async function RESENDCODE(userid){
	const response = await fetch('https://akybay-back.herokuapp.com/users/'+userid+'/resendCode',
	{
		method: 'GET',
        headers: { 'Content-Type': 'application/json' },
    });
    const json = await response.json();
    return json;
}


