import React from 'react';
import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './pages/Home';
import NotFound from './pages/NotFound';
import Login from "./pages/Login";
import Register from "./pages/Register";
import Place from "./pages/Place";
import { routes } from './util/RoutesHelper';
import EmailValidation from "./pages/EmailValidation";
import NewHome from "./pages/NewHome";
import QRReader from "./pages/QRReader";

const App = () => {
  return (
      <Router>
        <Switch>
          <Route exact={true} path={routes.home} component={Home} />
          <Route path={routes.login} component={Login} />
          <Route path={routes.register} component={Register} />
          <Route path={routes.place} component={Place} />
          <Route path={routes.emailValidation} component={EmailValidation} />
          <Route path={routes.newHome} component={NewHome} />
          <Route path={routes.qrReader} component={QRReader} />
          <Route component={NotFound} />
        </Switch>
      </Router>
  )
}

export default App;
