import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button, Grid } from "@material-ui/core";
import logo from "../assets/logo.svg";
import { Formik } from "formik";
import * as Yup from 'yup';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useState,useRef,useEffect } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { useHistory } from 'react-router-dom';
//import { useMutation } from "@apollo/react-hooks";
import { RESENDCODE, VALIDATEUSER } from '../services/UserService';
import { routeTo } from "../util/RoutesHelper";
import ServerErrorMessage from "../components/ServerErrorMessage";
import LoadingSpinner from '../components/LoadingSpinner';

const EmailValidation = (props) => {
  const [inputValue, setInputValue] = useState('');
  const [inputValue2, setInputValue2] = useState('');
  const [inputValue3, setInputValue3] = useState('');
  const [count, setCount] = useState(0);
  const [isFocused, setFocus] = useState(false);
  const [showPassword, changeShowPassword] = useState(false);
  const [errorMessages, setErrorMessages] = useState(null);
  const [loading, setLoading] = useState(false);
  const [login, { error }] = useState(null);
  const digit1 = useRef(null);
  const digit2 = useRef(null);
  const digit3 = useRef(null);
  const digit4 = useRef(null);
  useEffect(() => {
    digit2.current = inputValue; // Write it to the ref
});
  let history = useHistory();

 const handleFocus = React.useCallback(
   (val) =>{ 
    setInputValue(val);
    document.getElementById('digit2').focus();  
    },[digit1]
 )

 const handleFocus2 = React.useCallback(
  (val) =>{ 
   setInputValue2(val);
   document.getElementById('digit3').focus();  
   },[digit2]
)

const handleFocus3 = React.useCallback(
  (val) =>{ 
   setInputValue3(val);
   document.getElementById('digit4').focus();  
   },[digit3]
)

  if (error) return <ServerErrorMessage />
  

  const handleSubmit =  (values) => {   
    setLoading(true);
    var code = ""+document.getElementById('digit1').value+document.getElementById('digit2').value+document.getElementById('digit3').value+document.getElementById('digit4').value
    console.log(code);
    const validation = VALIDATEUSER(code,"37");
    validation.then(res => console.log(res));
    setLoading(false);
  }

  const handleResend = () => {
    const code = RESENDCODE("37");
    code.then(res => console.log(res));
  }

  const handleHome = () => {
    routeTo.home(history);
  }



  
  
  return (
    <LoginContainer maxWidth="sm">
      <br />
      <Formik
        initialValues={{
          digit1: "",
          digit2: "",
          digit3: "",
          digit4: ""
        }}
        onSubmit={async values => await handleSubmit(values)}
        validationSchema={Yup.object().shape({
        })}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit,
          } = props;
          return (
            <form autoComplete="off" onSubmit={handleSubmit}>
              <h1>Email validation</h1>
                <SubtitleContainer>
                 Please enter the four digit code that was sent to your email
                </SubtitleContainer>
              <FormContainer>
                {errorMessages ?
                  <FeedbackContainer>{errorMessages}</FeedbackContainer> : <></>
                }<br />
                
                <br/>
                <Grid container  spacing={0}>
                  
                  <Grid item xs={3}>
                <TextFieldCustom
                  required
                  ref={digit1}
                  id="digit1"
                  defaultValue=""
                  onChange={(e) => handleFocus(e.target.value)}
                  inputProps={{ maxLength: 1 ,style: { textAlign: 'center' }}}
                  variant="outlined"
                  value={inputValue}
                />
                </Grid>
                <Grid item xs={3}>
                <TextFieldCustom
                  required
                  name="digit2"
                  ref={digit2}
                  id="digit2"
                  defaultValue=""
                  inputProps={{ maxLength: 1 ,style: { textAlign: 'center' }}}
                  variant="outlined"
                  onChange={(e) => handleFocus2(e.target.value)}
                  value={inputValue2}
                />
                </Grid>
                <Grid item xs={3}>
                <TextFieldCustom
                  required
                  id="digit3"
                  defaultValue=""
                  inputProps={{ maxLength: 1 ,style: { textAlign: 'center' }}}
                  variant="outlined"
                  onChange={(e) => handleFocus3(e.target.value)}
                />
                </Grid>
                <Grid item xs={3}>
                <TextFieldCustom
                  required
                  id="digit4"
                  defaultValue=""
                  inputProps={{ maxLength: 1 ,style: { textAlign: 'center'}}}
                  variant="outlined"
                  
                />
                <br/>
                
                <br/>
                </Grid>
                <Grid item xs={12}>
                {loading ?
                  <LoadingSpinner loading={loading} /> :
                  <ButtonCustom
                    variant="contained"
                    disabled={isSubmitting}
                    //type="submit"
                    onClick={handleSubmit}
                    disableElevation

                  >
                    Continue
                  </ButtonCustom>
                }
                </Grid>
                </Grid>
                
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br />
                <Footer variant="h6">
                    
                    <br/>
                    <button onClick={handleResend}>
                      Resend Code
                    </button>
                  </Footer>
              </FormContainer>
            </form>
          );
        }}
      </Formik>
    </LoginContainer >
  );
};

const LoginContainer = styled(Container)`
  && {
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    height: 100vh;
  }
`
const Logo = styled.img`
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  margin-bottom: 60px;
  cursor: pointer;
  width: 75%;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  
  margin-left: auto;
  margin-right: auto;
`;

const ButtonCustom = styled(Button)`
  margin-top: 10px;
  && {
    border-radius: 60px;
    min-width: 110px;
    background: #e24449;
    color: white;
    font-weight : bold;
    margin-left: 20px;
    :hover {
      background: #e24449;
      color: white;
    }
    width: 85%;
    height: 150%
  }
`;

const FeedbackContainer = styled.div`
  color: red;
`
const TextFieldCustom = styled(TextField)`
  width: 60%;
  height: 70%;
  border-radius: 60px;
  text-align: center;
`

const Footer = styled(Typography)`
  && {
    color: rgba(0, 0, 0, 0.57);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 2%;
    button {
      color: red;
      border: none;
      border-bottom: none;
      padding: 0px;
      margin-left: 5px;
      background-color: transparent;
      font-size: 1.8rem;
      outline: none;
      cursor: pointer;
      font-weight : bold;
    }
    button:hover{
      color:#e24449;
    }
  }
`

const SubtitleContainer = styled(Typography)`
  && {
    margin-left: auto;
    margin-right: auto;
    text-align: left;
  }
`;


export default EmailValidation;

