import React, { useContext } from "react";
import {
    IconButton
} from "@material-ui/core";
import InfoIcon from '@material-ui/icons/Info';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import styled from 'styled-components';
import { useHistory } from "react-router";
import { CartContext } from '../contexts/CartContext';
import { routeTo } from '../util/RoutesHelper';
import logo from "../assets/logo.svg";

const PlaceItem = (props) => {
    const history = useHistory();

    const { addProduct, cartItems, increase } = useContext(CartContext);

    const isInCart = () => {
        return !!cartItems.find(item => item.id === props.product.id);
    }

    const handleAddProduct = (product) => {
        if(isInCart(product)){
            increase(product);
        }else{
            addProduct(product);
        }
    }

    return (
        <PlaceItemWrapper onClick={() => handleAddProduct(props.product)}>
            <img  src={"https://placeralplato.com/files/2016/01/Pizza-con-pepperoni-640x480.jpg?width=1200&enable=upscale"} alt={"foto"} />
            <ImageBottom
                title={props.product.name}
                subtitle={<span>{props.product.price}</span>}
                actionIcon={
                    <IconButton aria-label={`info`} >
                        <InfoIcon style={{ color: 'black' }} />
                    </IconButton>
                }
            />
        </PlaceItemWrapper>
    );
};

const PlaceItemWrapper = styled.div`
    margin: 4px;
    width: 100%;
    position: relative;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    img {
        width: 90%;
        border-radius: 25px;
    }

`;

const ImageBottom = styled(GridListTileBar)`
    && {
        width: 90%;
        left: inherit;
        right: inherit;
        visibility: hidden;
        background-color: rgb(245,245,245, 0.8);
        .MuiGridListTileBar-title, .MuiGridListTileBar-subtitle{
            color: black;
        }
        border-radius: 0px 0px 25px 25px;
        ${PlaceItemWrapper}:hover & {
            visibility: visible;
        }
        @media screen and (max-width: 1100px){
            visibility: visible;
        }
    }
`;

export default PlaceItem;