import * as React from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button, Grid } from "@material-ui/core";
import logo from '../assets/logo.svg';
import { Formik } from "formik";
import * as Yup from "yup";
import InputAdornment from '@material-ui/core/InputAdornment';
import { makeStyles } from '@material-ui/core/styles';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { useHistory } from 'react-router-dom';
//import { useMutation } from "@apollo/react-hooks";
import { REGISTER } from '../services/UserService';
import { routeTo } from '../util/RoutesHelper';
import ServerErrorMessage from "../components/ServerErrorMessage";
import LoadingSpinner from '../components/LoadingSpinner';
import { countries } from '../data/Dummy';

const RegisterForm = (props) => {
  const [showPassword, changeShowPassword] = useState(false);
  const [showRePassword, changeShowRePassword] = useState(false);
  const [errorMessages, setErrorMessages] = useState(null);
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  let error = false;
  let register;
  if (error) return <ServerErrorMessage />

  const handleSubmit = async (values) => {
    setLoading(true);
    const user = REGISTER(values.username,values.password,values.email,values.country_code,values.zip_code);
    console.log(values);  
    user.then(res =>{
      console.log(res);
      if(res.error == undefined){
        //routeTo.emailValidation(history);
      }else{
        console.log(res.error);
      };
    });
    setLoading(false);
  }

  const handleLoginClick = () => {
    if (props && props.onLoginClick) {
      props.onLoginClick();
    } else {
      routeTo.login(history);
    }
  }

  const handleHome = () => {
    routeTo.home(history);
  }

  function countryToFlag(isoCode) {
    return typeof String.fromCodePoint !== 'undefined'
      ? isoCode
          .toUpperCase()
          .replace(/./g, (char) => String.fromCodePoint(char.charCodeAt(0) + 127397))
      : isoCode;
  }

  function countryToCode(actCountry) {
    var i;
    var ret;
    var found = false;
    for(i = 0; i < countries.length && !found ; i++){
      var country = countries[i];
      if(country.label==actCountry){
        ret = country.code;
        found = true;
      }else{
        ret = "not found";
      }
    }
    return ret;
  }

  return (
    <RegisterContainer  maxWidth="sm">
      <Logo src={logo} onClick={handleHome} />
      <BackToLoginWrapper>
        Already registered?
        <button onClick={handleLoginClick}>
          Login here
        </button>
      </BackToLoginWrapper>
      <br />
      <Formik
        initialValues={{ email: "", username: "", password: "", repeatPassword: "", zip_code: "", country_code: "" }}
        onSubmit={async values => {
          await handleSubmit(values)
        }}
        validationSchema={Yup.object().shape({
          email: Yup.string()
            .email()
            .required("Required"),
          username: Yup.string().required("Required"),
          password: Yup.string().required("Required"),
          repeatPassword: Yup.string().equalTo(Yup.ref('password'), 'Passwords must match').required('Required'),
          zip_code: Yup.string().required("Required"),
          country_code: Yup.string().required("Required")
        })}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit,
          } = props;
          return (
            <form autoComplete="off" onSubmit={handleSubmit}>
              <FormContainer>
                <Grid container spacing={2}>
                {errorMessages ?
                  <FeedbackContainer>{errorMessages}</FeedbackContainer> : <></>
                }<br />
                <GridCustom item xs={6}>
                  <Autocomplete
                    id="country_code"
                    options={countries}
                    autoHighlight
                    onChange={handleChange}
                    onBlur={handleBlur}
                    getItemValue={(option) => option.code}
                    getOptionLabel={(option) => option.label}
                    renderOption={(option) => (
                      <React.Fragment>
                        <span>{countryToFlag(option.code)}</span>
                        {option.label} ({option.code}) +{option.phone}
                      </React.Fragment>
                    )}
                    renderInput={(params) => (
                      <TextFieldCustom
                        {...params}
                        label="Choose a country"
                        variant="outlined"
                        onChange={values.country_code = countryToCode(params.inputProps.value)}
                        inputProps={{
                          ...params.inputProps,
                          autoComplete: 'new-password',
                        }}
                      />
                    )}
                  />
                  
                  {errors.country_code && touched.country_code && (
                  <FeedbackContainer>{errors.country_code}</FeedbackContainer>
                  )}
                </GridCustom>
                <GridCustom item xs={6} >
                <TextFieldCustom
                  id="zip_code"
                  label="Zip"
                  variant="outlined"
                  placeholder="Enter your zip code"
                  type="text"
                  value={values.zip_code}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.zip_code && touched.zip_code
                      ? "text-input error"
                      : "text-input"
                  }
                />
                {errors.zip_code && touched.zip_code && (
                  <FeedbackContainer>{errors.zip_code}</FeedbackContainer>
                )}
                </GridCustom>
                <Grid item xs={12}>
                <TextFieldCustom
                  id="username"
                  label="Username"
                  margin="normal"
                  variant="outlined"
                  placeholder="John123"
                  type="text"
                  value={values.username}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.username && touched.username
                      ? "text-input error"
                      : "text-input"
                  }
                />
                {errors.username && touched.username && (
                  <FeedbackContainer>{errors.username}</FeedbackContainer>
                )}
                </Grid>
                <Grid item xs={12}>
                <TextFieldCustom
                  id="email"
                  label="Email"
                  margin="normal"
                  variant="outlined"
                  placeholder="example@domain.com"
                  type="text"
                  value={values.email}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.email && touched.email
                      ? "text-input error"
                      : "text-input"
                  }

                />
                {errors.email && touched.email && (
                  <FeedbackContainer>{errors.email}</FeedbackContainer>
                )}
                </Grid>
                <Grid item xs={12}>
                <TextFieldCustom
                  id="password"
                  label="Password"
                  margin="normal"
                  variant="outlined"
                  placeholder="Enter your password"
                  type={showPassword ? 'text' : 'password'}
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.password && touched.password
                      ? "text-input error"
                      : "text-input"
                  }
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="Toggle password visibility"
                          onClick={() => changeShowPassword(!showPassword)}
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                />
                {errors.password && touched.password && (
                  <FeedbackContainer>{errors.password}</FeedbackContainer>
                )}
                </Grid>
                <Grid item xs={12}>
                <TextFieldCustom
                  id="repeatPassword"
                  label="Repeat Password"
                  margin="normal"
                  variant="outlined"
                  placeholder="Re-enter your password"
                  type={showRePassword ? 'text' : 'password'}
                  value={values.repeatPassword}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.repeatPassword && touched.repeatPassword
                      ? "text-input error"
                      : "text-input"
                  }
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="Toggle password visibility"
                          onClick={() => changeShowRePassword(!showRePassword)}
                        >
                          {showRePassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                />
                {errors.repeatPassword && touched.repeatPassword && (
                  <FeedbackContainer>{errors.repeatPassword}</FeedbackContainer>
                )}
                </Grid>
                <br />
                </Grid>
                <br />
                {loading ?
                  <LoadingSpinner loading={loading} /> :
                  <ButtonCustom
                    variant="contained"
                    disabled={isSubmitting}
                    type="submit"
                    disableElevation
                  >
                    Create
                </ButtonCustom>
                }
              </FormContainer>
            </form>
          );
        }}
      </Formik>
    </RegisterContainer >
  );
};

const GridCustom = styled(Grid)`
  spacing: 2;
`

const TextFieldCustom = styled(TextField)`
  width: 100%;
`

const RegisterContainer = styled(Container)`
    && {
      display: flex;
      flex-direction: column;
      text-align: left;
      justify-content: center;
      height: 100vh;
    }
`;

const FormContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
`;

const FeedbackContainer = styled.div`
    text-align: center;
    color: red;
`;

const Logo = styled.img`
  margin-top: 25%;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  margin-bottom: 40px;
  cursor: pointer;
`;

const ButtonCustom = styled(Button)`
  margin-top: 10px;
  && {
    border-radius: 60px;
    min-width: 110px;
    background: red;
    color: white;
    font-weight : bold;
    :hover {
      background: #e24449;
      color: white;
    }
  }
`;

const BackToLoginWrapper = styled(Typography)`
  && {
    color: rgba(0, 0, 0, 0.57);
    margin-top: 5px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    button {
      color: #1D2C3C;
      border: none;
      border-bottom: 1px solid black;
      padding: 0px;
      margin-left: 5px;
      background-color: transparent;
      font-size: 1.2rem;
      outline: none;
      cursor: pointer;
    }
  }
`;

const equalTo = (ref, msg) => {
  return Yup.mixed().test({
    name: 'equalTo',
    exclusive: false,
    // eslint-disable-next-line no-template-curly-in-string
    message: msg || '${path} must be the same as ${reference}',
    params: {
      reference: ref.path,
    },
    test: function (value) {
      return value === this.resolve(ref);
    },
  });
};
Yup.addMethod(Yup.string, 'equalTo', equalTo);

export default RegisterForm;