export const routes = {
    home: '/',
    login: '/login',
    notFound: '/notFound',
    register: '/register',
    place: '/place',
    emailValidation: '/validation',
    newHome: '/newHome',
    qrReader: '/qrReader'
}

const createRouteWithoutParam = (pathName) => (history) =>
    history.push({
        pathname: pathName,
    })

const createRouteWithoutParamReplace = (pathName) => (history) =>
    history.replace({
        pathname: pathName,
    })

export const routeTo = {
    login: createRouteWithoutParamReplace(routes.login),
    home: createRouteWithoutParam(routes.home),
    notFound: createRouteWithoutParam(routes.home),
    register: createRouteWithoutParam(routes.register),
    place: createRouteWithoutParam(routes.place),
    emailValidation: createRouteWithoutParam(routes.emailValidation),
    newHome: createRouteWithoutParam(routes.newHome),
    qrReader: createRouteWithoutParam(routes.qrReader)
}